body{
    overflow: auto;
}
body::-webkit-scrollbar {

	-ms-overflow-style: none;  /* IE and Edge */
	overflow-x: hidden;
	scroll-behavior: smooth;
	width: 5px;


}
body::-webkit-scrollbar-track {
	background: #f1f1f1;
	overflow-x: hidden;
  }
body::-webkit-scrollbar-thumb {
	background: #888;
	overflow-x: hidden;
  }
