.info1 h1 {
	text-align: center;
	margin-top: 1;
	color: white;
	font-family: "Century Gothic", "Lato", sans-serif;
	width: 100%;

}
.info1 p {
	display: flex;
	font-size: 1.1rem;
	text-align: center;
	color: #7395ae;
}
.info1 {
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content: center;
	justify-items: center;
	height: 100%;
	align-items: center;

}
.info1 .card {
	display: flex;
	flex-direction: column;
	justify-content: center;
	justify-items: center;
	align-items: center;
	border-radius: 3px;
	background: rgba(0, 0, 0, 0.2);
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
	min-height: 55vh;
	width: 50%;
}
.info1 .in {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 1%;
	width: 25%;

}
.info1 .il {
	display: flex;
	justify-content: start;
	align-items: start;
	height: 100%;
	width: 100%;
	padding: 1%;
}

a {
	text-decoration: none;
}
a:hover {
	text-decoration: underline;
	text-decoration-color: #7395ae;
}
