.contactI {
	display: flex;
	flex-direction: row;
	justify-content: center;
	justify-items: center;
	align-items: center;
	border-radius: 3px;
	background: rgba(0, 0, 0, 0.2);
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
	height: 65vh;
	width: 75%;
}
.frame {
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content: center;
	justify-items: center;
	height: 100%;
	align-items: center;
}
.contactI a {

	font-size: 1.1rem;
	text-align: center;
	color: #87b4ab;
}
.contactI h1 {

	font-size: 1.1rem;
	text-align: center;
	color: white;
}
.contactI p {

	font-size: 1.1rem;
	text-align: center;
	color: white;
}
.contactI .sectionTitle {
	justify-self: center;
	align-items: center;
	width: 100%;
	height: 25%;
	padding-bottom: 2%;
}
.contactI .sectionImage {
	width: 100%;
	height: 75%;
}
.contactI img {
	object-fit: contain;
	width: 100%;
	height: 100%;
}
