.Icard {
	display: flex;
	flex-direction: row;
	width: 100%;

	margin: 0;
	box-sizing: border-box;
	justify-content: center;
	text-align: center;
}
.introI {
	display: flex;
	width: 50%;
	flex-direction: column;
	justify-content: center;
	border-top-right-radius: 5%;
	border-bottom-right-radius: 5%;
	background: rgba(0, 0, 0, 0.2);
}
.head {
	padding-left: 1%;

	color: #87b4ab;
	font-family: "Century Gothic", "Lato", sans-serif;
}
.image {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1%;
	width: 15%;
	height: 10%;
	border-bottom-left-radius: 5%;
	border-top-left-radius: 5%;
	background: rgba(0, 0, 0, 0.2);
}
.para {
	justify-content: center;
	color: white;
	font-size: 1.1rem;
	text-align: center;
}
.image img {
	height: 100%;
	width: 100%;
	border-radius: 5%;
	overflow-clip-margin: unset;
	overflow: auto;
	object-fit: contain;
}
@media screen and (max-width: 800px) {
	.Icard {
		margin: 0;
		display: flex;
		flex-direction: column;
		max-width: 100%;
		padding-top: 2%;
		align-items: center;
		justify-content: center;
		text-align: center;
	}
	.introI {
		display: flex;
		flex-direction: column;
		max-width: 100%;
	}
	.image {
		margin: 0;
		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 3px;
		width: 50%;
		height: 40%;

		object-fit: fill;
	}
	.head {
		padding: 0;
		color: #87b4ab;

		font-family: "Century Gothic", "Lato", sans-serif;
	}
}
